import React from 'react';
import * as R from 'ramda';

import Commodity from './Commodity';

import './Exchange.css';

const lcmSet = (multiplicands) => {
    /*function range(min, max) {
        var arr = [];
        for (var i = min; i <= max; i++) {
            arr.push(i);
        }
        return arr;
    }*/
    
    const gcd = (a, b) => !b ? a : gcd(b, a % b);

    const lcm = (a, b) => (a * b) / gcd(a, b);
    

    
    return multiplicands.reduce(lcm);
}




export default function Exchange() {
    const [labourHours, setLabourHours] = React.useState([1, 2, 3, 4]);
    const commodities = [
        {
            name: "bread",
            singular: "Loaf of Bread",
            plural: "Loaves of Bread"
         },
         {
             name: "corn",
             singular: "Ear of Corn",
             plural: "Ears of Corn"
         },
         {
             name: "brick",
             singular: "Brick",
             plural: "Bricks"
         },
         {
             name: "soap",
             singular: "Bar of Soap",
             plural: "Bars of Soap"
         }
        ]
    
    const getCommodityString = (index, number) => (number === 1) ? commodities[index].singular : commodities[index].plural;

    const getHoursSetter = (index) => {
        return (e) => {
            var arr = Array.from(labourHours);
            arr[index] = Number(e.target.value);
            setLabourHours(arr);
        }
    }

    const getExchangeQuantities = () => {
        const lcm = lcmSet(labourHours);
        return labourHours.map((item, index) => lcm/item);
    }

    
    return (
        <div className="Exchange">
            <div className="Exchange-params">
            
                <div className="Exchange-inputs">
                    {labourHours.map((item, index) => {
                        return (
                            <input 
                            onChange={getHoursSetter(index)} 
                            value={item} 
                            name={commodities[index].name}
                            key={index} 
                            type="range" min={1} max={8} step={1} />
                    
                    )})}
                </div>
                <div className="Exchange-labels">
                    {labourHours.map((item, index) => {
                        return (
                            <label 
                            htmlFor={commodities[index].name}
                            key={index}>
                                {item} hours per {commodities[index].singular} 
                            </label>
                    )})}
                    
                </div>
            

            </div>
            <div className="Exchange-outputs">
                {getExchangeQuantities().map((item, index) => {
                    return (
                        <div className="Exchange-pile" key={index}>
                            <p key={index}>{item} {getCommodityString(index, item)}</p>
                            {R.range(0, item).map((i, j) => {
                                return (<Commodity name={commodities[index].name} key={j} />)
                                
                            })}
                        </div>

                    )

                })}
                
                
            </div>
        </div>
    );
}